*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  scroll-behavior: smooth;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto Condensed", "Roboto", "Arial Narrow", sans-serif;
  color: #102654;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
}

a {
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1160px;
  margin: 0 auto;
}
@media (max-width: 1199px) {
  .container {
    min-width: 768px;
    max-width: 1199px;
    padding-left: 45px;
    padding-right: 45px;
  }
}
@media (max-width: 767px) {
  .container {
    min-width: 320px;
    max-width: 767px;
    padding: 0 15px;
  }
}

.header {
  padding-top: 30px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
@media (max-width: 1199px) {
  .header {
    padding-top: 40px;
  }
}
@media (max-width: 767px) {
  .header {
    padding-top: 20px;
  }
}

.header__link {
  font-size: 26px;
  letter-spacing: 0.03em;
  color: #ffffff;
  font-family: "BN", tahoma, sans-serif;
  display: block;
}
@media (max-width: 1199px) {
  .header__link {
    font-size: 24px;
    line-height: 24px;
    letter-spacing: 0.02em;
  }
}
@media (max-width: 767px) {
  .header__link {
    font-size: 20px;
    line-height: 20px;
  }
}

@media (max-width: 1199px) {
  .header img {
    width: 82px;
    height: 17px;
  }
}
@media (max-width: 767px) {
  .header img {
    width: 68px;
    height: 15px;
  }
}

.caption {
  position: relative;
  background: conic-gradient(from 130.31deg at 16.03% 39.06%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
  min-height: 658px;
  top: -54px;
  left: 0;
  right: 0;
  z-index: -1;
}
@media (max-width: 1199px) {
  .caption {
    min-height: 554px;
  }
}
@media (max-width: 767px) {
  .caption {
    min-height: 491px;
  }
}

.caption__wrapper {
  padding-top: 51px;
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  grid-column-gap: 64px;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .caption__wrapper {
    padding-top: 82px;
    grid-template-columns: repeat(auto-fit, 324px);
    grid-column-gap: 30px;
  }
}
@media (max-width: 767px) {
  .caption__wrapper {
    grid-template-columns: 290px;
    justify-content: end;
  }
}

.caption__block {
  padding-top: 117px;
  padding-left: 100px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
@media (max-width: 1199px) {
  .caption__block {
    padding-left: 0;
    padding-top: 53px;
  }
}
@media (max-width: 767px) {
  .caption__block {
    padding-top: 112px;
  }
}

.caption__block span {
  font-family: "BN", tahoma, sans-serif;
  font-size: 49px;
  line-height: 59px;
  letter-spacing: 0.03em;
  display: block;
  text-align: right;
}
@media (max-width: 1199px) {
  .caption__block span {
    font-size: 34px;
    line-height: 41px;
  }
}
@media (max-width: 767px) {
  .caption__block span {
    font-size: 28px;
    line-height: 34px;
  }
}

.caption__block h1 {
  margin: 0;
  font-family: "BN", tahoma, sans-serif;
  font-size: 109px;
  line-height: 109px;
  font-weight: 700;
  letter-spacing: 0.03em;
  margin-top: 6px;
  text-align: right;
  margin-bottom: 52px;
}
@media (max-width: 1199px) {
  .caption__block h1 {
    font-size: 86px;
    line-height: 86px;
    margin-top: 4px;
    margin-bottom: 34px;
  }
}
@media (max-width: 767px) {
  .caption__block h1 {
    font-size: 78px;
    line-height: 78px;
    margin-top: 10px;
    margin-bottom: 39px;
  }
}

.caption__block ul {
  position: absolute;
  font-size: 14px;
  line-height: 21px;
  top: 55%;
  left: 20px;
  text-align: right;
  list-style: none;
  padding: 0;
  margin: 0;
}
@media (max-width: 1199px) {
  .caption__block ul {
    top: 47%;
    left: 0;
  }
}
@media (max-width: 767px) {
  .caption__block ul {
    top: 57%;
  }
}

.caption a {
  display: block;
  color: #ffffff;
  font-size: 12px;
  line-height: 14px;
}

.caption__block a {
  text-transform: uppercase;
  letter-spacing: 0.05em;
  padding: 20px;
  font-weight: 700;
  border: 4px solid #ffffff;
  width: 230px;
  text-align: center;
}
@media (max-width: 1199px) {
  .caption__block a {
    width: 183px;
  }
}
@media (max-width: 767px) {
  .caption__block a {
    width: 290px;
  }
}

.caption__list {
  display: flex;
  font-weight: 400;
  flex-direction: column;
  gap: 20px;
  align-self: end;
  align-items: flex-end;
  list-style: none;
  padding: 0;
  margin: 0;
  padding-bottom: 40px;
}
@media (max-width: 1199px) {
  .caption__list {
    display: none;
  }
}

@media (max-width: 767px) {
  .caption img {
    display: none;
  }
}

.description {
  margin-top: 66px;
  padding: 0 calc(50% - 580px);
  display: grid;
  grid-template-columns: 1fr auto;
  grid-column-gap: 85px;
}